import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http';
import { CommandsProvider } from '../../providers/commands/commands';


/**
 * Generated class for the ChangePasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-change-password',
  templateUrl: 'change-password.html',
})
export class ChangePasswordPage {

  public message         : string;
  private passActual     : string;
  private passNew        : string;
  private passNewConfirm : string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public utils: UtilsProvider, public http: HttpProvider,
              public command: CommandsProvider) {
  
  
  }

 //metodo para validar que las contraseñas sean las mismas
  public validatedPassword(): boolean{

    var condicion: boolean;

    if(this.passNew != this.passNewConfirm){
      this.utils.alertCampos("changePassword.validatedPass");
      condicion = false;
    }else{
      condicion = true
    }
    return condicion;

  }

  public validarCamposVacios(){
    if(this.passActual == null && this.passNew == null && this.passNewConfirm == null){
      this.utils.alertCampos("changePassword.validatedCampos");
    }
  }


  //Metodo para hacer el cambio de contraseña que se llama en el onClick
  public changePassword(){

    if(this.passActual != null && this.passNew != null && this.passNewConfirm != null){
        var cambiarPass: boolean;
        cambiarPass = this.validatedPassword();
        if(cambiarPass == true){
          this.loadChangePassword();
          //alert("Entro a cambiarla");
        }
    }
    else{
      this.utils.alertCampos("changePassword.validatedCampos");
    }

    

  }


  private loadChangePassword() {
    
    var command: string[] = this.utils.getCommands(this.command.E2MOMR80);
    //var command = 'E2MOMR80'; 
    var passActual  = this.passActual;
    var passNew     = this.passNew;
    var passConfirm = this.passNewConfirm;
    var Cpachap     = "";

    //var userLogin:string = use;
    //var passw = this.utils.encodePass(this.pass); 
    //var user = this.utils.encodeUser(userLogin);     

  // var command :string[] =  this.utils.getCommands(this.command.E2LSCR89L);
             //command[0]
   var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID=' + this.utils.getSessionId() + '&E2EW=null' + 
              this.utils.separator + 'Enviar' + this.utils.separator + this.utils.separator + this.utils.separator +'0'+ this.utils.separator + 
              this.utils.separator + '&PAR=' + passActual + this.utils.separator + passNew + this.utils.separator + passConfirm+ this.utils.separator + Cpachap + this.utils.separator;
    this.http.callServer(param).then(res => {     
      this.message = res.message;
      
      if (this.message != '' && typeof this.message!=="undefined") {
        this.utils.alertServidor(this.message);
        if(this.message == "Blocked User"){
          this.utils.logout();
        }
        if(this.message == "Password was update correctly"){
          this.utils.logout();
        }
      } else {
        
      }
    },
      error => {
        console.log(error);
      });
      
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
  }




}



